@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 10rem minmax(0, 1fr) 1rem;
  grid-column-gap: 1.5rem;
  align-items: flex-end;
}

.preview {
  width: 100%;
  height: 5rem;
  object-fit: cover;
}

.spinner {
  width: 2rem;
  fill: $colorHighlight;
}

.name {
  @include regular($colorHighlight);

  overflow: hidden;
  text-overflow: ellipsis;
}

.button {
  @include resetButton;

  margin-left: auto;
}

.icon {
  margin-bottom: 0.5rem;
  width: 100%;
  fill: $colorHighlight;
}
