@import "styles/variables";
@import "styles/mixins";

.wrapper {
  @include regular($colorHighlight, 500);

  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  line-height: 1;
  text-align: center;
  transition-duration: $transitionDuration;
  transition-property: color, border, background-color;
}

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  pointer-events: none;
}

.overlayVisible {
  pointer-events: all;
}

.extensions {
  @include regular;

  margin-top: 0.4rem;
  font-weight: 300;
  transition-duration: $transitionDuration;
  transition-property: color;
}
