@import "styles/mixins";
@import "styles/variables";

.file {
  overflow: hidden;
  text-overflow: ellipsis;
}

.list {
  @include resetList;

  margin-top: 2rem;
}

.item {
  &:not(:first-child) {
    margin-top: 2rem;
  }
}

.preview {
  width: 100%;
}

.label {
  @include regular($colorHighlight, 500);

  display: flex;
  align-items: center;
}

.icon {
  margin-right: 1rem;
  width: 2.4rem;
  fill: $colorHighlight;
}