.wrapper {
  position: relative;
  padding-right: 15rem;
  padding-left: 15rem;
}

.slider {
  margin-top: 6rem;
  padding-bottom: 5rem;
}

.image {
  width: 100%;
}

.fullImage {
  max-width: 90vw;
  max-height: 90vh;
}

.button {
  position: absolute;
  top: 23rem;

  &:not(.buttonNext) {
    left: 0;
  }

  &.buttonNext {
    right: 0;
  }
}