@import "styles/variables";
@import "styles/mixins";

.wrapper {
  margin-bottom: -1rem;
  background-size: 100% 80rem;
  background-repeat: no-repeat;
  overflow: hidden;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 81rem;
}

.heading {
  @include visuallyHidden;
}

.button {
  @include resetButton;
  @include h2($colorLightest);
}

.indicator {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 3.2rem;
    right: -4.5rem;
    border-top: 1.2rem solid transparent;
    border-left: 2rem solid $colorLightest;
    border-bottom: 1.2rem solid transparent;
    transform: translateX(0);
    transition-duration: $transitionDuration;
    transition-property: transform;
    pointer-events: none;
  }

  &:hover::before {
    transform: translateX(-2rem);
  }

  &:active::before {
    transform: translateX(0);
  }
}

.list {
  position: absolute;
  top: 0;
  right: calc(50% - 94rem / 1.4);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 11rem 13rem 15rem 13rem;
  width: 94rem;
  height: 80rem;
  background-color: $colorHighlightLight;
  box-shadow: -0.6rem 0.6rem 0.5rem rgba(0, 0, 0, 0.2);
  transform: translateY(-110%);
  transition-duration: $transitionDuration;
  transition-property: transform;
}

.listOpened {
  transform: translateY(0);
}

.link {
  @include lang;

  display: block;
  color: $colorLightest;
  text-transform: uppercase;

  &::before {
    top: 0;
  }
}