@import "styles/variables";
@import "styles/mixins";

.section {
  border-top: 1px solid $colorDarkest;
  overflow: hidden;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr 10rem 60rem;
  padding-bottom: 5rem;
}

.backlink {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  margin-top: 8rem;
  width: max-content;
  justify-content: flex-start;
}

.content {
  position: relative;
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  margin-top: 5rem;
  padding: 3.6rem 6.5rem 3.6rem  0;
  background-color: $colorLightest;
  border-top-right-radius: 5rem;
  border-bottom-right-radius: 5rem;
  box-shadow: 0.8rem 0.8rem 0.8rem rgba(0, 0, 0, 0.15);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 100%;
    width: 50vw;
    height: 100%;
    background-color: $colorLightest;
    box-shadow: 0.8rem 0.8rem 0.8rem rgba(0, 0, 0, 0.15);
    z-index: -1;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.date {
  @include regular($colorMediumLight);
  @include decoratedItem;
}

.wyziwig {
  padding-top: 3rem;

  p {
    @include regular;

    &:first-child {
      margin: 0;
    }

    &:not(:first-child) {
      margin: 1.5rem 0 0 0;
    }
  }
}

.button {
  margin-top: 2rem;
}

.frame {
  grid-column: 2 / 4;
  grid-row: 1 / 4;
}

.image {
  width: 100%;
}
