@import "styles/variables";
@import "styles/mixins";

.label {
  @include regular;

  display: flex;
  align-items: center;
  cursor: pointer;
}

.button {
  @include resetButton;
}

.icon {
  margin-left: 4rem;
  width: 1.2rem;
  fill: $colorHighlight;
}

.iconPhoto {
  margin-right: 1rem;
  width: 2.4rem;
  fill: $colorHighlight;
}

.input {
  @include visuallyHidden;
}

