@import "styles/variables";
@import "styles/mixins";

.content {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

.heading {
  @include h3($colorLightest);

  text-transform: uppercase;
}

.duration {
  @include h0($colorLightest);

  margin-left: 4rem;
  letter-spacing: 1rem;
}