@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 90rem 30rem auto;
  justify-content: center;
  align-items: center;
  padding-top: 8rem;
  padding-bottom: 8rem;
}

.map,
.contacts {
  grid-row: 1 / 2;
}

.map {
  grid-column: 1 / 3;
  margin-left: auto;
  width: 100rem;
  height: 50rem;
}

.contacts {
  @include shadowStandard;

  grid-column: 2 / -1;
  position: relative;
  padding: 5rem 5rem 5rem 14rem;
  background-color: $colorLightest;
  border-radius: 5rem;
}

.list {
  margin-top: 6rem;
}

.item {
  @include regularBanner;

  position: relative;

  &:not(:first-child) {
    margin-top: 6.4rem;
  }
}

.icon {
  position: absolute;
  top: 0;
  left: -5rem;
  width: 3.2rem;
  fill: $colorHighlight;
}

.subitem {
  &:not(:first-child) {
    margin-top: 2rem;
  }
}
