@import "styles/variables";

.heading {
  padding-right: 70%;
}

.list {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $colorHighlight;
  }
}


@media (max-width: $adaptiveMax) {
  .heading {
    padding-right: 0;
    width: 20rem;
  }

  .list {
    &::before {
      display: none;
    }
  }

  .item {
    &:not(:first-child) {
      margin-top: 3rem;
    }
  }
}